"use client";
import Image from "next/image";

export default function Testimonials() {
  return (
    <section className="py-20 bg-white">
      <div className="mx-auto max-w-6xl px-6 text-center">
        {/* âœ… Section Heading */}
        <h2 className="text-3xl sm:text-4xl font-extrabold text-[#171717]">
          What our <span className="text-[var(--color-primary)]">Customers</span> say
        </h2>
        <p className="text-[#444444] mt-2 mb-10">
          Trusted by homeowners. Loved for convenience.
        </p>
      </div>

      {/* âœ… Full-width Responsive Image */}
      <div className="w-full flex justify-center">
        <div className="w-[95%] md:w-[90%] lg:w-[85%]">
          <Image
            src="/testimonials.png"
            alt="Customer testimonials collage"
            width={1800}
            height={900}
            priority
            className="w-full h-auto rounded-2xl shadow-md object-cover"
          />
        </div>
      </div>
    </section>
  );
}
              