"use client";

import { motion } from "framer-motion";
import Image from "next/image";
import Link from "next/link";

export default function Hero() {
  return (
    <section className="relative overflow-hidden bg-[var(--background)] text-[var(--foreground)]">
      {/* âœ… Background Image */}
      <motion.div
        initial={{ scale: 1.1, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        transition={{ duration: 1.2, ease: "easeOut" }}
        className="relative h-[620px] w-full"
      >
        <Image
          src="/hero.jpg"
          alt="Hero"
          fill
          priority
          className="object-cover brightness-[1.05] contrast-[1.05]"
        />
      </motion.div>

      {/* âœ… Floating blurred glow (uses primary color) */}
      <motion.div
        initial={{ opacity: 0, scale: 0.8 }}
        animate={{ opacity: 0.4, scale: 1 }}
        transition={{ duration: 2, ease: "easeOut" }}
        className="absolute -top-20 left-1/3 h-80 w-80 rounded-full bg-[color:var(--color-primary)]/40 blur-[100px]"
      />

      {/* âœ… Foreground content */}
      <div className="absolute inset-0 flex items-center">
        <div className="mx-auto max-w-7xl w-full px-4">
          <motion.div
            initial={{ opacity: 0, y: 40 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 1, delay: 0.3, ease: "easeOut" }}
            className="max-w-xl rounded-3xl bg-[var(--color-secondary)] backdrop-blur-md p-10 shadow-xl border border-white/30"
          >
            {/* âœ… Title */}
            <motion.h1
              className="text-3xl sm:text-4xl font-bold leading-tight text-black"
            >
              Join{" "}
              <span className="text-transparent bg-clip-text bg-gradient-to-r from-[var(--color-primary)] to-blue-600">
                Workly
              </span>{" "}
              â€“ Grow Your Skills, Earn on Your Terms
            </motion.h1>

            <motion.p className="mt-3 text-gray-700 text-lg">
              Discover your earning potential by completing tasks that match your skills.
            </motion.p>


            {/* âœ… CTA Button using primary/secondary colors */}
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.8, delay: 1 }}
              className="mt-6"
            >
              <Link
                href="/getstarted"
                className="inline-flex items-center gap-2 rounded-lg 
             px-6 py-3 font-medium text-[var(--color-secondary)]
             shadow-md transition-all duration-300 ease-out
             hover:scale-[1.02] active:scale-[0.97]"
                style={{
                  backgroundColor: "var(--color-primary)",
                }}
                onMouseEnter={(e) => {
                  (e.currentTarget as HTMLAnchorElement).style.backgroundColor = "#1E80E0"; // darker hover shade
                }}
                onMouseLeave={(e) => {
                  (e.currentTarget as HTMLAnchorElement).style.backgroundColor = "var(--color-primary)";
                }}
              >
                Get Started
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={2}
                  stroke="currentColor"
                  className="w-5 h-5"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M17.25 8.25L21 12m0 0l-3.75 3.75M21 12H3"
                  />
                </svg>
              </Link>

            </motion.div>
          </motion.div>
        </div>
      </div>

      {/* âœ… Scroll Indicator */}
      <motion.div
        initial={{ opacity: 0, y: -10 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ delay: 1.5 }}
        className="absolute bottom-6 left-1/2 transform -translate-x-1/2"
      >
        <div className="flex flex-col items-center text-[var(--color-secondary)]/70 text-sm">
          <motion.div
            animate={{ y: [0, 10, 0] }}
            transition={{ repeat: Infinity, duration: 1.5, ease: "easeInOut" }}
            className="w-[1px] h-6 bg-[var(--color-secondary)]/60 mt-1"
          />
        </div>
      </motion.div>
    </section>
  );
}
