"use client";
import Image from "next/image";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { useEffect, useState } from "react";
import { ArrowUp } from "lucide-react";
import { useTheme } from "next-themes";

const nav = [
  { href: "/contact", label: "Contact us" },
  { href: "/faqs", label: "FAQs" },
  { href: "/blog", label: "Blog" },
];

export default function Navbar() {
  const pathname = usePathname();
  const [showButton, setShowButton] = useState(false);
  const { theme } = useTheme();
  const [mounted, setMounted] = useState(false);

  // âœ… Show scroll-to-top button after scrolling down
  useEffect(() => {
    const handleScroll = () => setShowButton(window.scrollY > 300);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  // âœ… Smooth scroll-to-top
  const scrollToTop = () => window.scrollTo({ top: 0, behavior: "smooth" });

  // âœ… Avoid hydration mismatch
  useEffect(() => setMounted(true), []);
  if (!mounted) return null;

  return (
    <>
      {/* âœ… Fixed Navbar */}
      <header className="fixed top-0 left-0 right-0 z-50 bg-[var(--color-bg-neutral-50)]/90 backdrop-blur-md border-b border-[var(--color-btn-teritary-200)] shadow-sm">
        <div className="mx-auto max-w-7xl px-8 py-2 flex items-center justify-between">
          {/* âœ… Left: Logo */}
          <div
            onClick={scrollToTop}
            className="flex items-center cursor-pointer transition-transform duration-300 hover:scale-105"
          >
            <Image
              src="/logo.png"
              alt="Workly"
              width={120}
              height={40}
              priority
              className="object-contain"
            />
          </div>

          {/* âœ… Right: Navigation */}
          <nav className="flex items-center text-sm font-semibold text-[var(--color-text-primary)]">
            {nav.map((item, index) => (
              <div key={item.href} className="flex items-center">
                <Link
                  href={item.href}
                  className={`transition-colors duration-200 ${
                    pathname === item.href
                      ? "text-[var(--color-active-line)]"
                      : "text-[var(--color-text-secondary)] hover:text-[var(--color-text-primary)]"
                  }`}
                >
                  {item.label}
                </Link>

                {/* Separator dot */}
                {index < nav.length - 1 && (
                  <span className="mx-3 text-[var(--color-text-muted)] select-none">
                    Â·
                  </span>
                )}
              </div>
            ))}
          </nav>
        </div>
      </header>

      {/* âœ… Spacer (avoid content being hidden behind navbar) */}
      <div className="h-[50px]" />

      {/* âœ… Scroll-to-Top Button */}
      {showButton && (
        <button
          onClick={scrollToTop}
          aria-label="Scroll to top"
          className={`fixed bottom-6 right-6 z-50 flex items-center justify-center rounded-full 
          backdrop-blur-md bg-[var(--color-bg-neutral-50)]/80 border border-[var(--color-btn-teritary-200)] 
          shadow-md text-[var(--color-text-primary)] transition-all duration-300 
          hover:bg-[var(--color-btn-teritary-100)] hover:shadow-lg cursor-pointer
          ${
            showButton
              ? "opacity-100 translate-y-0"
              : "opacity-0 translate-y-6 pointer-events-none"
          }`}
          style={{ width: 50, height: 50 }}
        >
          <ArrowUp size={22} strokeWidth={2.5} />
        </button>
      )}
    </>
  );
}
