"use client";
import { motion } from "framer-motion";
import {
  UserPlus,
  Settings,
  ShieldCheck,
  CreditCard,
  MapPin,
  Briefcase,
} from "lucide-react";

const steps = [
  {
    icon: <UserPlus className="w-7 h-7 text-[var(--color-primary)]" />,
    title: "Create Your Account",
    desc: "Sign up easily with your email or phone number to access the Workly platform and begin your journey.",
  },
  {
    icon: <Settings className="w-7 h-7 text-[var(--color-primary)]" />,
    title: "Set Up Your Profile",
    desc: "Add your skills, experience, and services. Personalize your profile to attract local clients quickly.",
  },
  {
    icon: <ShieldCheck className="w-7 h-7 text-[var(--color-primary)]" />,
    title: "Complete Verification",
    desc: "Upload identification documents for a smooth and secure onboarding experience.",
  },
  {
    icon: <CreditCard className="w-7 h-7 text-[var(--color-primary)]" />,
    title: "Pay Activation Fee",
    desc: "A small one-time fee helps maintain quality and safety standards across the platform.",
  },
  {
    icon: <MapPin className="w-7 h-7 text-[var(--color-primary)]" />,
    title: "Choose Your Service Area",
    desc: "Pick the areas youâ€™d like to serve and set your own working hours for complete flexibility.",
  },
  {
    icon: <Briefcase className="w-7 h-7 text-[var(--color-primary)]" />,
    title: "Start Receiving Jobs",
    desc: "Once approved, receive job requests, choose what fits your schedule, and start earning instantly.",
  },
];

export default function Steps() {
  return (
    <section id="get-started" className="relative py-28 bg-white overflow-hidden">
      {/* âœ… Accent Glow */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 0.2 }}
        transition={{ duration: 1.5 }}
        className="absolute -top-24 right-1/3 w-[500px] h-[500px] bg-[var(--color-primary)]/25 blur-[120px] rounded-full pointer-events-none"
      />

      <div className="relative mx-auto max-w-6xl px-6">
        {/* âœ… Section Header */}
        <motion.div
          initial={{ opacity: 0, y: 40 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
          viewport={{ once: true }}
          className="text-center max-w-2xl mx-auto"
        >
          <h2 className="text-4xl md:text-5xl font-extrabold text-[#171717]">
            Getting Started with{" "}
            <span className="text-[var(--color-primary)] font-semibold">
              Workly
            </span>
          </h2>
          <p className="mt-4 text-[#171717cc] text-lg leading-relaxed">
            Set up your Workly profile in minutes â€” follow these simple steps to
            start offering your services and earning on your terms.
          </p>
        </motion.div>

        {/* âœ… Steps Grid */}
        <div className="mt-20 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-12 relative">
          {/* Connector line */}
          <div className="hidden lg:block absolute top-28 left-1/2 -translate-x-1/2 w-[75%] h-[2px] bg-[var(--color-primary)]/15"></div>

          {steps.map((step, i) => (
            <motion.div
              key={step.title}
              initial={{ opacity: 0, y: 60 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{
                duration: 0.8,
                delay: i * 0.15,
                ease: "easeOut",
              }}
              viewport={{ once: true }}
              whileHover={{
                scale: 1.06,
                rotate: 0.3,
                transition: { duration: 0.4 },
              }}
              className="relative flex flex-col items-center text-center bg-white rounded-3xl border border-gray-100 p-10 shadow-sm hover:shadow-2xl transition-all duration-300 group"
            >
              {/* âœ… Step Number */}
              <div className="absolute -top-6 left-1/2 -translate-x-1/2 bg-white border border-[var(--color-primary)]/25 rounded-full w-12 h-12 flex items-center justify-center font-bold text-[var(--color-primary)] shadow-md group-hover:bg-[var(--color-primary)]/10 transition">
                {i + 1}
              </div>

              {/* âœ… Icon */}
              <motion.div
                whileHover={{ scale: 1.15, rotate: 3 }}
                transition={{ type: "spring", stiffness: 250 }}
                className="flex items-center justify-center h-16 w-16 mb-6 rounded-2xl bg-[var(--color-primary)]/10 group-hover:bg-[var(--color-primary)]/20 transition-all"
              >
                {step.icon}
              </motion.div>

              {/* âœ… Step Title */}
              <h3 className="text-lg font-semibold text-[#171717] tracking-tight">
                {step.title}
              </h3>

              {/* âœ… Step Description */}
              <p className="mt-3 text-[#444444] text-sm leading-relaxed max-w-xs">
                {step.desc}
              </p>

              {/* âœ… Bottom Accent */}
              <motion.div
                initial={{ scaleX: 0 }}
                whileInView={{ scaleX: 1 }}
                transition={{ duration: 0.8, delay: 0.3 }}
                viewport={{ once: true }}
                className="mt-6 w-16 h-[3px] bg-[var(--color-primary)] rounded-full origin-left"
              />
            </motion.div>
          ))}
        </div>
      </div>
    </section>
  );
}
